export default{
	data:function(){
		return {
			shopList:{},
			cbd:{}
		}
	},
	onLoad:function(){
		this.getPage()
	},
	methods:{
		goShop:function(shopid){
			uni.navigateTo({
				url:"../../pageb2b/b2b_shop/index?shopid="+shopid
			})
		},
		getPage:function(){
			var that=this;
			that.app.get({
				url:that.app.apiHost+"/module.php?m=b2b_cbd&a=show&cbdid=3",
				success:function(res){
					that.shopList=res.data.list;
					that.cbd=res.data.cbd;
					uni.setNavigationBarTitle({
						title:res.data.cbd.title
					})
				}
			})
		}
	}
}
